<template>
  <div class="warps">
    <my-el-header>   <el-button @click.nav.native="resetConfigure" type="primary" icon="el-icon-edit" size="small"></el-button> </my-el-header>
    <div class="container">
      <div class="form-nav">
        <el-form :inline="true" :model="form" ref="form" class="demo-form-inline" :rules="rule">
          <el-form-item label="" prop="fifoName">
            <el-input v-model="form.fifoName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="fifoAgent">
            <el-input v-model="form.fifoAgent" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="fifoRouterIn">
            <el-input v-model="form.fifoRouterIn" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="agent" prop="memberTimeout">
            <el-input v-model="form.memberTimeout" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="find"></el-button>
            <el-button @click="resetForm('clear')"></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary"  @click="addForms(null, '')"></el-button>
      </div>
      <el-dialog :close-on-click-modal="false" :title="title"  destroy-on-close ref="addForm" :visible.sync="dialogFormVisible">
        <el-form :model="addForm" ref="addForm" :rules="rules">
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoName">
              <el-input v-model="addForm.fifoName" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoAgent">
              <el-input v-model="addForm.fifoAgent" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoRouterIn">
              <el-select v-model="addForm.fifoRouterIn" placeholder="" style="width: 100%" >
                <el-option
                  v-for="item in fifoRouterInList"
                  :key="item.id"
                  :label="item.directoryNumber"
                  :value="item.directoryNumber">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoRouterOut">
              <el-select v-model="addForm.fifoRouterOut" placeholder="" style="width: 100%" >
                <el-option
                  v-for="item in fifoRouterOutList"
                  :key="item.id"
                  :label="item.directoryNumber"
                  :value="item.directoryNumber">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoWaitMusic">
              <el-upload
                :action="uploadFileUrl"
                :limit="limit"
                :with-credentials="true"
                :show-file-list="false"
                :before-upload="handleBeforeUpload"
                :headers="headers"
                :on-success="handleUploadSuccess"
                :on-error="handleUploadError"
              >
                <el-button size="small" type="primary" style="margin-right: 20px"></el-button>
                <div class="el-upload__tip" slot="tip" v-if="addForm.fifoWaitMusic.length === 0">
                  
                  <template v-if="fileSize">  <b style="color: #f56c6c">{{ fileSize }}MB</b> </template>
                  <template v-if="fileType">  <b style="color: #f56c6c">{{ fileType.join("/") }}</b> </template>
                  
                </div>
                <div class="el-upload__tip" slot="tip" v-else>
                 :  {{addForm.fifoWaitMusic}}
                </div>
              </el-upload>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="memberSimultaneous">
              <el-input v-model="addForm.memberSimultaneous" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="memberTimeout">
              <el-input v-model="addForm.memberTimeout" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="wrapupTime">
              <el-input v-model="addForm.wrapupTime" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoEmergency">
              <el-select v-model="addForm.fifoEmergency" placeholder="" style="width: 100%" >
                <el-option
                  v-for="item in fifoEmergencyList"
                  :key="item.id"
                  :label="item.directoryNumber"
                  :value="item.directoryNumber">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoEmergency">
              <el-input v-model="addForm.domain" placeholder=""></el-input>
            </el-form-item>

          </div>
          <div class="width">
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoEmergency">
              <el-input v-model="addForm.fifoNight" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="fifoEmergency">
              <treeselect v-model="deptId" :multiple="false" :options="treeArr" :normalizer="normalizer" placeholder=""/>
            </el-form-item>

          </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </div>
      </el-dialog>
      <el-table
        height="calc(100vh - 100px - 100px - 100px - 100px)"
        :header-cell-style="{background:'#ccc', color: '#fff',}"
        class="table"
        :data="list"
        v-if="list.length !== 0"
        style="width: 100%">
        <el-table-column
          align="center"
          prop="date"
          label="">
          <template scope="scope">{{scope.$index + 1}}</template>
        </el-table-column>
        <el-table-column
          prop="fifoName"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="fifoAgent"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="fifoRouterIn"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="fifoRouterOut"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="memberSimultaneous"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="memberSimultaneous"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="memberTimeout"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="wrapupTime"
          label="">
        </el-table-column>
        <el-table-column
          align="center"
          prop="wrapupTime"
          fixed="right"
          min-width="100px"
          label="">
          <template scope="scope">
            <div class="operate">
              <el-link @click="addForms(scope.row, '')" style="margin-right: 20px"></el-link>
              <template>
                <el-popconfirm title="" @confirm="del(scope.row)">
                  <el-link  slot="reference">
                  </el-link>
                </el-popconfirm>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <my-empty v-else/>
    </div>
    <my-footer v-on:next = "next" @prev="prev" :form="form" @change="change"  @pageCheng="pageCheng"></my-footer>
  </div>
</template>

<script>
import { addFifo, delFifo, getDirectory, getFifo, upDataFifo } from "@/newwork/directory";
import myEmpty from "@/newwork/myEmpty";
import { getDictionaryAll, getOrganizeList } from "@/newwork/system-colltroner";
import { fn } from "@/uti";
import myTree from "@/components/myTree";
import myFooter from "@/components/myFooter";
import { upDataFile } from "@/newwork/ground-colltroner";
import myElHeader from "@/components/myElHeader";
import { delPbxConfigure } from "@/newwork/conferencr";
import { getCookie } from "@/auth";
import treeselect from "@riophae/vue-treeselect";
// import the styles
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "queue",
  data(){
    return {
      normalizer(node) {
        if (node.children && !node.children.length) {
          delete node.children;
        }
        return {
          id: node.deptId,
          children: node.children,
          label: node.deptName
        };
      },
      form: {
        fifoName: '', //
        fifoAgent: '',//
        fifoRouterIn: '',  //fifoRouterIn
        memberTimeout: '', //	agent
      },
      treeArr: [],
      addForm: {
        domain: '', //
        fifoAgent: '', //
        fifoName: '', //
        fifoRouterIn: '', //
        fifoRouterOut: '', //
        fifoWaitMusic: '', //
        memberSimultaneous: '', //agent1
        memberTimeout: '', //agent
        wrapupTime : '', //
        fifoEmergency: '', //
        fifoNight: ''
      },
      fifoRouterInList: [],  //
      fifoRouterOutList: [], //
      fifoEmergencyList: [], //
      fileData: '', //
      list: [],
      dialogFormVisible: false,
      title: '',
      formLabelWidth: '120px',
      rules: {
        fifoName: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        fifoAgent: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        fifoRouterIn: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        fifoRouterOut: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        fifoWaitMusic: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        memberSimultaneous: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        memberTimeout: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        wrapupTime: [
          { required: true, message: ',', trigger: 'blur' },
        ],
        fifoEmergency: [
          { required: true, message: ',', trigger: 'blur' },
        ],
      domain: [
          { required: true, message: ',', trigger: 'blur' },
        ],
      },
      rule: {
        fifoName: [
          { required: false, message: ',', trigger: 'blur' },
        ],
        fifoAgent: [
          { required: false, message: ',', trigger: 'blur' },
        ],
        fifoRouterIn: [
          { required: false, message: ',', trigger: 'blur' },
        ],
        memberTimeout: [
          { required: false, message: ',', trigger: 'blur' },
        ],
      },
      limit: 1,
      uploadFileUrl: 'http://localhost:8080/dispatch/file/upload',
      headers: {
        Authorization: "Bearer " + getCookie(),
      },
      fileSize: 5,
      fileType: ['avi']
    }
  },
  methods: {
    getSelectedValue(value) {
      this.addForm.deptId = value.deptId
    },
    pageCheng(e){
      this.form = this.$options.data().form
      this.form.pageSize = e
      this.getFifo(this.form)
    },

    next(){
      this.form.pageNum ++
      console.log(this.form);
      this.getUserAll(this.form)
    },
    prev(){
      this.form.pageNum --
      this.getUserAll(this.form)
    },
    change(e){
      console.log(e);
      this.form.pageNum = e
      this.getUserAll(this.form)
    },
    handleBeforeUpload(file){
      console.log(file);
      this.fileData = file
      if (this.fileType) {
        let fileExtension = "";
        if (file.name.lastIndexOf(".") > -1) {
          fileExtension = file.name.slice(file.name.lastIndexOf(".") + 1);
        }
        const isTypeOk = this.fileType.some((type) => {
          if (file.type.indexOf(type) > -1) return true;
          if (fileExtension && fileExtension.indexOf(type) > -1) return true;
          return false;
        });
        if (!isTypeOk) {
          this.$message.error(`, ${this.fileType.join("/")}!`);
          return false;
        }
      }
      // 
      if (this.fileSize) {
        const isLt = file.size / 1024 / 1024 < this.fileSize;
        if (!isLt) {
          this.$message.error(` ${this.fileSize} MB!`);
          return false;
        }
      }
      return true;
    },
    handleUploadError(){
      this.$message.error(", ");
    },
    handleUploadSuccess(res){
      if (res.code === 200){
        this.$message.success("");
        this.addForm.fifoWaitMusic = this.fileData.name
      }else {
        this.$message.error(", ");
      }
    },
    addForms(row, type){
      this.title = type
      this.getFifo(this.form)
      this.dialogFormVisible = true
      if(type === ''){
        this.addForm = this.$options.data().addForm
      }else {
        this.addForm = row
        console.log(row);
      }
    },
    resetForm(type) {
     type === 'clear' ? this.$refs.form.resetFields(): this.$refs.addForm.resetFields();
      this.getFifo(this.form)
    },
    find(){
      this.getFifo(this.form)
    },
    submitForm(){
      this.$refs.addForm.validate((valid) => {
        if (valid) {
      this.title === '' ? addFifo(this.addForm).then(res => {
          if(res.data.code === 200){
            this.getFifo(this.form)
            this.$message.success('')
            this.dialogFormVisible = false
          }else {
            this.$message.error(res.data.msg)
          }
        }) : upDataFifo(this.addForm).then(res => {
          if(res.data.code === 200){
            this.getFifo(this.form)
            this.$message.success('')
            this.dialogFormVisible = false

          }else {
            this.$message.error(res.data.msg)
          }
        })
        } else {
          return false;
        }
      });

    },
    del(row){
      delFifo(row.id).then(res => {
        if(res.data.code === 200){
          this.$message.success('')
          this.getFifo(this.form)
        }else {
          this.$message.error(res.data.msg)
        }
      })
    },
    getFifo(form){
      getFifo(form).then(res => {
        if(res.data.code === 200){
          this.$bus.$emit('total', res.data.data.total)
          this.list = res.data.data.records
        }else {
          this.$message.error(res.data.msg)
        }
      }).catch(e => this.$message.error(e))
    },
    //  

    /**
     * @param type === 1   === 4  === 5 
     */

    getDirectory(type){
      const data = {}
      data.type = type
      if(type === 4 ){
        getDirectory(data).then(res => {
          this.fifoRouterInList = res.data.data.records
        })
      }else if(type === 5) {
        getDirectory(data).then(res => {
          this.fifoRouterOutList = res.data.data.records
        })
      }else {
        getDirectory(data).then(res => {
          this.fifoEmergencyList = res.data.data.records
        })
      }
    },

    getOrganizeList(){
      getOrganizeList().then(res => {
        this.treeArr = fn(res.data.data);
      });
    },
    resetConfigure(){
      delPbxConfigure('fifo').then(res => {
        if(res.data.code === 200 ){
          this.$message.success('')
        }else {
          this.$message.error(res.data.msg)
        }
      })
    }


  },
  created() {
    this.getFifo(this.form)
    this.getOrganizeList()
  },
  components: {
    myEmpty,
    myTree,
    myFooter,
    myElHeader,
    treeselect
  },
  destroyed(){
    this.$bus.$off('pageChang')
  },
  watch: {
    dialogFormVisible (val){
      if(!val){
        this.addForm = this.$options.data().addForm
      }else {
        //, 
        this.getDirectory(4)
        this.getDirectory(5)
        this.getDirectory(0)
        //
      }
    }
  },
  computed: {
    deptId: {
      get(){
          return this.addForm.deptId + ''
      },
      set(val){
        this.addForm.deptId = val
      }
    }
  }
}
</script>

<style scoped>
.form-nav{
  display: flex;
  height: 40px;
  justify-content: space-between;
}
.table{
  margin-top: 20px;
}
.width{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.width > * {
  flex: 1;
}
</style>
